.img-container {
  width: 350px;
  border: 2px solid #000;
  border-radius: 15px;
  margin: 0 auto;
  overflow: hidden;
}

.img-big-container {
  width: 550px;
  border: 2px solid #000;
  border-radius: 15px;
  margin: 0 auto;
  overflow: hidden;
}

.grow_up-big-img {
  transition: width 2s, height 2s, transform 12s;
}

.grow_up-big-img:hover {
  width: 100%;
  height: 100%;
}

